/* montserrat-italic - latin */
@font-face {
   font-family: 'Montserrat-italic';
   font-style: italic;
   font-weight: 400;
   src: url('assets/fonts/montserrat/montserrat-v14-latin-italic.eot'); /* IE9 Compat Modes */
   src: local('Montserrat Italic'), local('Montserrat-Italic'),
        url('assets/fonts/montserrat/montserrat-v14-latin-italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
        url('assets/fonts/montserrat/montserrat-v14-latin-italic.woff2') format('woff2'), /* Super Modern Browsers */
        url('assets/fonts/montserrat/montserrat-v14-latin-italic.woff') format('woff'), /* Modern Browsers */
        url('assets/fonts/montserrat/montserrat-v14-latin-italic.ttf') format('truetype'), /* Safari, Android, iOS */
        url('assets/fonts/montserrat/montserrat-v14-latin-italic.svg#Montserrat') format('svg'); /* Legacy iOS */
 }
